// Component that maps content based on the selected itemId
const getContentComponent = (itemId) => {
    switch (itemId) {
        case '2':
            return <AboutHawkClient />;
        case '4':
            return <Download />;
        case '5':
            return <CreateWorkspace />;
        case '6':
            return <CreateCollection />;
        case '7':
            return <CreateRequest />;
        case '8':
            return <Scripting />;
        case '9':
            return <Validations />;
        case '10':
            return <VariablesContent />;
        default:
            return <Typography>Select a topic to view details.</Typography>;
    }
};

const Docs = () => {
    const [selectedItemId, setSelectedItemId] = useState(null);

    // Recursive function to render tree items
    const renderTreeItems = (nodes) =>
        nodes.map((node) => (
            <TreeItem
                key={node.itemId}
                itemId={node.itemId}
                label={node.label}
                nodeId={node.itemId}
            >
                {node.children ? renderTreeItems(node.children) : null}
            </TreeItem>
        ));

    // Handle item selection
    const handleNodeSelect = (event, itemId) => {
        const node = findNodeById(treeData, itemId);
        if (!node.children) {
            // Only update content if it's a leaf node
            setSelectedItemId(itemId);
        }
    };

    // Find a node by its itemId
    const findNodeById = (nodes, id) => {
        for (let node of nodes) {
            if (node.itemId === id) return node;
            if (node.children) {
                const found = findNodeById(node.children, id);
                if (found) return found;
            }
        }
        return null;
    };

    return (
        <Box sx={{ display: 'flex', height: '100vh' }}>
            {/* Left Panel - Tree View */}
            <Box sx={{ width: '300px', borderRight: '1px solid #ddd', p: 2 }}>
                <SimpleTreeView
                    slots={{ expandIcon: ChevronRight, collapseIcon: ExpandMore }}
                    onSelectedItemsChange={handleNodeSelect}
                >
                    {renderTreeItems(treeData)}
                </SimpleTreeView>
            </Box>

            {/* Right Panel - Content View */}
            <Box sx={{ flex: 1, p: 3, overflow: 'scroll' }}>
                {getContentComponent(selectedItemId)}
            </Box>
        </Box>
    );
};

export default Docs;
